<template lang="pug">
.content
    .row
      .col-md-12
        .mg-r-10.md-button.btn.btn-primary(@click='addNewPromotion') Thm mi khuyn mi
        .promotion-wrapper
          .promotion-item(v-for='pro_item in list_promotion')
            .image
              img.img(:src='pro_item.imageUrl')
            .title {{ pro_item.title }}
            .subtitle {{ pro_item.subTitle }}
            .from-date-to-date {{ pro_item.availableFrom|dateTimeFormat }}  -  {{ pro_item.availableTo|dateTimeFormat }}
            .percent-down Gim {{ pro_item.percentDown }} %
            .content {{ pro_item.content }}


</template>

<script>
  import axios from 'axios'

  export default {
    data() {
      return {
        list_promotion: []
      }
    },
    filters: {
      dateTimeFormat: function (value) {
        var today = new Date(value)
        var dd = today.getDate()
        var mm = today.getMonth() + 1

        var yyyy = today.getFullYear()
        if (dd < 10) {
          dd = '0' + dd
        }
        if (mm < 10) {
          mm = '0' + mm
        }
        var todayr = dd + '/' + mm + '/' + yyyy
        return todayr
      }
    },
    methods: {
      getListPromotion() {
        var self = this

        axios.get('admin/promotion/list/', {
          headers: {Authorization: self.$store.state.userToken}
        })
          .then(response => {
            self.list_promotion = response.data.content
          })
      },
      addNewPromotion() {

      }
    },
    created() {
      this.getListPromotion()
    }
  }
</script>
<style lang="sass">

</style>




